<template>
    <div class="au">
        授权中...
    </div>
</template>

<script>
import { getQueryString } from '@/utils/utils'
export default {
  name: 'Author',
  data () {
    return {}
  },
  created () {
        // 判断链接有没有token参数，如果没有就跳微信授权，然后后台会重定向回来并携带token，如： www.xxxx.com/wx/author?token=xxxxxxxxx&msg=200
        let getTk = window.localStorage.getItem('token')
        if (!getTk) {
            console.log('授权登录')
            if(getQueryString('wechat_login') || this.$route.query['wechat_login']){
                this.login()
            } else {
                this.returnGetCodeUrl()
            }
        }
  },
  methods: {
    async login(){
        let params = {
            code: getQueryString('code')
        }
        let { data } = await this.$api.weLogin(params)
        const token = data.token
        // 存储token到本地
        window.localStorage.setItem('token', token)
        // 获取beforeLoginUrl 页面地址
        const url = window.localStorage.getItem('beforeLoginUrl')
        console.log(url, token, 333)
        // 跳转
        this.$router.replace(url)
        // 删除本地beforeLoginUrl
        window.localStorage.removeItem('beforeLoginUrl')
    },
    async returnGetCodeUrl () {
      let { data } = await this.$api.getWxAuth()
      //data.url='https://open.weixin.qq.com/connect/oauth2/authorize?
      //appid=wxd59a85a105f1c2fc&redirect_uri=happy.supdatasys.cn/wechat_login&
      //response_type=code&scope=snsapi_userinfo&state=STATE&uin=&key=&connect_redirect=1#wechat_redirect'
      if (data.status === 200) {
        window.location.href = data.url
      }
    }
  }
}
</script>

<style scoped>
.au{
    position: absolute;
    z-index: 40;
    top: 0;
    height: 100%;
    width: 100%;
    background: #FDC7A1;
    text-align: center;
    line-height: 5.33333rem;
    font-family: NotoSerifCJKscBlack;
    color: #716b64;
    }
</style>



