import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import First from '@/components/First'
import Second from '@/components/Second'
import NeedWechat from '@/components/NeedWechat'
import Author from '@/components/Author'
import store from '../store'
import * as weixin from '@/utils/weixin'


// 获取访问的user-agent
let ua = navigator.userAgent.toLowerCase() || window.navigator.userAgent.toLowerCase()
    // 判断user-agent
export const platform = {
    isApp: /creditaccelerator/i.test(ua), // APP
    isWX: /MicroMessenger/i.test(ua), // 微信端，（排除掉现金巴士app&好人好信app的坑）
    isIOS: /i(Phone|Pad|Pod|OS)/i.test(ua), // 苹果家族
    isAndroid: /(android|nexus)/i.test(ua), // 安卓家族
    isWindows: /(Windows Phone|windows[\s+]phone)/i.test(ua), // 微软家族
    isBlackBerry: /BlackBerry/i.test(ua), // 黑莓家族
}

if (platform.isWX) {
    weixin.init()
    weixin.onShare()
}

Vue.use(Router)
const isFree = true
const ROUTER = new Router({
    routes: [{
        path: '*',
        redirect: '/'
    }, {
        path: '/',
        name: 'Index',
        component: Index
    }, {
        path: '/first',
        name: 'First',
        component: First
    }, {
        path: '/sec',
        name: 'Second',
        component: Second
    }, {
        path: '/author',
        name: 'Author',
        component: Author
    }, {
        path: '/needWeixin',
        name: 'needWeixin',
        component: NeedWechat,
        meta: {
            isFree
        }
    }]
})
ROUTER.beforeEach((to, from, next) => {
    /* 路由发生变化修改页面title */
    // document.title = '上海银商，送福万家'
    //     // 第一次访问
    // const token = window.localStorage.getItem('token')
    // if (!token && to.path !== '/author') {
    //     // 保存用户进入的url
    //     console.log(to.fullPath)
    //     window.localStorage.setItem('beforeLoginUrl', to.fullPath)
    //     next('/author')
    // } else if (token && !store.getters.userInfo) {
    //     // 拉取用户信息
    //     store.dispatch('getUserInfo').then(() => {
    //         next()
    //     }).catch(err => {
    //         console.log(err)
    //         window.localStorage.removeItem('token')
    //     })
    // } else {
    //     // 已经登录
    //     next()
    // }
    next()
})
export default ROUTER



