<template>
    <div class="sec" :class="{over:overf}">
        <div class="content zoomIn">
            <p>祝：<br/>{{msg}}</p>
            <div class="btn btn1" @click="rand()">
                <img src="~@/assets/images/btn1@2x.png" alt="">
            </div>
            <div class="btn btn2" @click="sendWish()">
                <img src="~@/assets/images/btn2@2x.png" alt="">
            </div>
        </div>
        <div class="result" ref="copyCardArea" id="copyCardArea" :class="{isTop:isTop}">
            <img src="~@/assets/images/result@2x.png" class="bgimg" alt="">
            <img :src="userInfo.headimgurl" v-if="userInfo.headimgurl" class="headimg"/>
            <span v-if="userInfo.username">{{userInfo.username}}</span>
            <p>祝：<br/>{{msg}}</p>
        </div>
        <div class="result2" ref="copyCardArea2" id="copyCardArea2" :class="{isTop2:isTop}">
            <!-- <span class="fix">长按保存图片</span> -->
            <img class="resimg" :src="state.imgUrl" alt="">
        </div>
         <transition name="fade">
            <div v-if="tranShow" class="modal" @touchstart="tranShow = false" :class="{isTop:isTop}">
                <p class="zoomIn">长按保存图片</p>
            </div>
        </transition>
    </div>
</template>

<script>
import html2canvas from 'html2canvas'
export default {
    name: 'sec',
    data () {
        return {
            msg:'新的一年越努⼒越幸福，越勇敢越好命。',
            isTop:false,
            blessing: [
                '新的一年越努⼒越幸福，越勇敢越好命。',
                '新的一年有我在，你的幸福有我守护。',
                '一年之际在于春，新年新目标，加油吧少年！',
                '新年万事顺心，岁⽉静好，祝全家身体安康。 ',
                '过去的一年没实现的愿望，新的一年都实现。',
                '今年一定要比去年更幸福呦～',
                '鸿运当头，福气冲天',
                '惜福于心，迎难而上',
                '春回大地，福满人间',
                '福星高照，万事亨通',
                '门迎百福，户纳千祥',
                '人臻五福，花满三春'
            ],
            state: {
                isDownloadImg: false,
                imgUrl: ''
            },
            userInfo:{
                headimgurl: require('@/assets/images/head@2x.png'),
                username: 'hiyou'
            },
            headimg: require('@/assets/images/head@2x.png'),
            nickname: 'hiyou',
            tranShow: false,
            overf:true
        }
    },
    created() {
        console.log(this.$store.getters.userInfo)
        if(this.$store.getters.userInfo) {
            this.userInfo = this.$store.getters.userInfo
        }
        // environment
        // expires
        // headimgurl
        // openid
        // username:
    },
    methods:{
        rand() {
            let len = this.blessing.length
            let randi = Math.floor(Math.random() * len)
            console.log(randi)
            this.msg = this.blessing[randi]
        },
        sendWish() {
            window.scrollTo(0, 0)
            this.isTop = true
            this.tranShow = true
            setTimeout(() => {
                self.tranShow = false
            }, 500)
            this.overf = false
            let b64
            let self = this
            html2canvas(this.$refs.copyCardArea, {
                useCORS: true
            }).then(canvas => {
                try {
                    b64 = canvas.toDataURL('image/png').replace('image/png', 'image/octet-stream')
                } catch (err) {
                    console.log(err)
                    alert(err)
                }
                this.state = {
                    imgUrl: b64,
                    isDownloadImg: true
                }
                // window.location.href= b64;

                // this.$refs.copyCardArea2.appendChild(canvas)
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.over{
    overflow-y: hidden;
}
.sec{
    font-family: NotoSerifCJKscBlack;
    background-color: #E7351F;
    background-image: url('~@/assets/images/sec@2x.png');
    width: 375px;
    height: 667px;
    position: absolute;
    background-size: cover;

    .secimg{
        width: 375px;
        height: 667px;
    }
    img{
        position: absolute;
    }
    .txt20{
        width: 95.5px;
        height: 12px;
        top:121.5px;
        left: 138.5px;
    }

    .content{
        width: 302.5px;
        height: 328.5px;
        background-image: url(~@/assets/images/letterbg@2x.png);
        position: absolute;
        top:149px;
        left: 50%;
        margin-left:-150px;
        padding-top: 80px;
        background-size: cover;
        z-index: 1;

        p{
            color: #e14b34;
            font-size: 18px;
            line-height: 25px;
            width: 180px;
            margin: auto;
            text-align: left;
            padding-left: 5px;
            letter-spacing: 1px;
            font-weight: bold;
        }
    }

    .btn{
        width: 113px;
        height: 114px;
        background-image: url('~@/assets/images/btnbg@2x.png');
        background-size: contain;
        position: absolute;

        img{
            width: 36px;
            height: 43px;
            position: absolute;
            left: 40px;
            top:31px;
        }
    }
    .btn1{
        left: 29px;
        bottom: 29px;
    }
    .btn2{
        right: 31px;
        bottom: 29px;
    }
    .result{
        width: 375px;
        height: 667px;
        top: 0;
        position: absolute;
        z-index: -1;
        padding-top: 296px;

        p{
            color: #DF4022;
            width: 180px;
            margin: auto;
            font-size: 18px;
            line-height: 25px;
            position: absolute;
            left: 50%;
            margin-left: -90px;
            font-weight: bold;
            letter-spacing: 1px;
        }
        .bgimg{
            position: absolute;
            width: 375px;
            height: 667px;
            top:0;
        }
        .headimg{
            position: absolute;
            width: 56px;
            border-radius: 40px;
            top: 59px;
            left: 50%;
            margin-left: -28px;
        }
        span{
            color: #fff;
            font-size: 18px;
            position: absolute;
            top: 127px;
            display: block;
            text-align: center;
            width: 375px;
        }
    }
    .result2{
        width: 375px;
        height: 667px;
        background-size: contain;
        top: 0;
        position: absolute;
        z-index: -1;
    }
    .isTop{
        z-index: 3;
    }
    .isTop2{
        z-index: 5!important;
    }
    .resimg{
        position: absolute;
        width: 375px;
        height: 667px;
        top: 0;
    }

    .fix{
        top: 12px;
        display: block;
        text-align: center;
        width: 375px;
        position: absolute;
        color: #EDA371;
        font-size: 12px;
    }

    .modal{
        position: fixed;
        width: 375px;
        height: 667px;
        background: #0000004f;
        top: 0;
        color: #fff;
        text-align: center;
        padding-top: 300px;

        p{
            background-color: #0000008a;
            border-radius: 6px;
            margin: auto;
            width: 120px;
            line-height: 45px;
        }
    }
}
</style>



