export let defaultConfig = {
    title: '上海银商，送福万家',
    link: 'http://happy.supdatasys.cn',
    desc: '上海银商祝福大家新春快乐，阖家幸福！',
    imgUrl: location.origin + require('@/assets/images/share@2x.png'),
    success: function(res) {
        // realTimeNSLog.info('wxShareSuccess', 'phoneNo')
    },
    cancel: function(res) {
        // realTimeNSLog.info('wxShareCancel', 'phoneNo')
    },
    fail: function(res) {
        // realTimeNSLog.info('wxShareFail', 'phoneNo')
        alert("分享失败！请刷新页面重新分享！")
    }
}

export function onShare(config) {
    let shareConfig = Object.assign({}, defaultConfig, config)

    wx.ready(function() { //微信初始化完成
        wx.onMenuShareTimeline(shareConfig);
        wx.onMenuShareAppMessage(shareConfig);
        wx.onMenuShareQQ(shareConfig);
        wx.onMenuShareWeibo(shareConfig);
    });
}


