<template>
  <div id="app">
    <router-view/>
    <img src="~@/assets/images/close@2x.png" v-show="!play" class="contr" @click="musicPlay" alt="">
    <img src="~@/assets/images/open@2x.png" ref="playbtn" v-show="play" class="contr turn" @click="musicPlay" alt="">
    <audio class="media-audio" controls loop ref="MusicPlay">
        <source src="~@/assets/images/music.mp3">
    </audio>
    <!-- <audio controls autoplay loop>
        <source src="~@/assets/images/music.mp3" >
        您的浏览器不支持 audio 元素。
    </audio> -->
  </div>
</template>

<script>
export default {
    name: 'App',
    data() {
        return {
            play:false
        }
    },
    methods:{
        musicPlay() {
            if(this.play){
                 this.$refs.MusicPlay.pause()
                 this.play = false
            }else{
                this.$refs.MusicPlay.play()
                this.play = true
            }
        }
    }
}
</script>

<style>
@font-face {
    font-family: NotoSerifCJKscBlack;
    src: url('./assets/fonts/NotoSerifCJKsc-Black.ttf')
}
.contr{
    width: 82px;
    height: 82px;
    position: fixed;
    right: 0;
    top: 14.5px;
    z-index: 6;
}
.media-audio{
    position: absolute;
    position: absolute;
    z-index: -2;
    opacity: 0;
}
</style>



